@import "./settings.scss";

* {
    box-sizing: border-box;
}

html, body { 
    margin:0;
    padding:0;
    width:100%;
    height:100%;
    overflow: auto;
    // font-family: "-apple-system", "Segoe UI", "Roboto, Noto" "Sans", "Ubuntu", "Cantarell", "Helvetica Neue";
    font-family: 'helvetica';
    color: #c48852;
    background-color: $primary;
}

button {
    cursor: pointer;
}

button:disabled {
    cursor: default;
}

a {
    color: white;
}

p {
    line-height: 1.35;
}

textarea {
    font-family: 'helvetica';
}

h1, h2, h3 {
    color: white;
}

.card {
    background-color: $dark-primary;
    padding: 20px 40px 20px 40px;
    margin: 20px;
    border-radius: 10px;
    min-height: 600px;
    height: fit-content;
    min-width: 440px;
    width: fit-content;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
}

.card-title {
    width: 100%;
    text-align: center;
    padding-bottom: $sizing-2;
    font-size: $type-sizing-5;
    font-weight: 500;
    margin-top: 0;
    border-bottom: 2px solid $accent;
    color: $accent-2;
}

.custom-tooltip {
    background-color: white;
    border-radius: 7px;
    padding: 1px 10px 1px 10px;
}

.button-structure {
    padding: 6px;
    border-radius: 5px;
    font-size: $type-sizing-3;
}

.button {
    @extend .button-structure;
    background-color: transparent;
    border: 1px solid $accent;
    color: $accent;
}

.button:hover {
    background-color: $accent;
    color: $accent-2;
}

.button-dlt {
    @extend .button-structure;
    border: 1px solid #c23b22;
    background-color: #c23b22;
    color: white;
}

.button-pos {
    @extend .button-structure;
    background-color: #618B25;
    color: $accent-2;
    border: 1px solid #618B25;
}

.label {
    font-size: $type-sizing-3;
}